import React, { useState, Fragment, useEffect } from "react"

import axios from "axios"
import ItemTable from "./components/ItemTable"
import ItemTotal from "./components/ItemTotal"
import DefectOptions from "./components/DefectOptions"
import DefectList from "./components/DefectList"
import ShowAlert from "./components/ShowAlert"
import CheckList from "./components/CheckList"
import UploadForm from "./components/UploadForm"
import SearchKey from "./components/SearchKey"
import MainMenu from "./components/MainMenu"
import SearchedMC from "./components/SearchedMC"
import SupplierInfo from "./components/SupplierInfo"
// import PhotoBlock from "./components/PhotoBlock"
import MfrSelection from "./components/MfrSelection"
import Misc from "./components/Misc"
import Result from "./components/Result"
import ChangeTrack from "./components/ChangeTrack"
import SharePoint from "./components/SharePoint"
import ShowSharePointFiles from "./components/ShowSharePointFiles"

import { v4 as uuidv4 } from "uuid"
import { trackPromise } from "react-promise-tracker"
import ShowSpinner from "./components/ShowSpinner"

const App = () => {
  //const initialFormState = { id: null, mc_no: "", item_no: "", order_qty: "", ship_qty: "" }
  const initialInspectionID = { mc: "", iteration: "Select", type: "Select" }

  // Setting state
  const [items, setItems] = useState([])
  const [showSearchedMC, setShowSearchedMC] = useState(false)
  const [defects, setDefects] = useState([])
  // set position 100 as impossible range as equivalent to empty
  const [alert, setAlert] = useState({ position: 100 })
  const [mcTable, setMCTable] = useState([])
  const [checkList, setCheckList] = useState([])
  const [inspectionDate, setInspectionDate] = useState(new Date())
  const [inspectionID, setInspectionID] = useState(initialInspectionID)
  const [uploads, setUploads] = useState([])
  const [userProfile, setUserProfile] = useState([])
  const [currentSU, setCurrentSU] = useState("")
  const [currentMF, setCurrentMF] = useState("")
  const [searchedMC, setSearchedMC] = useState([])
  const [itemSearchString, setItemSearchString] = useState("")
  const [partyTable, setPartyTable] = useState([])
  const [currentSUName, setCurrentSUName] = useState("")
  const [currentMFName, setCurrentMFName] = useState("")
  const [selectingSupplier, setSelectingSupplier] = useState(true)
  const [mfList, setMfList] = useState([])
  const [aqlTable, setAqlTable] = useState([])
  const [editMode, setEditMode] = useState(false)
  const [misc, setMisc] = useState({})
  const [main, setMain] = useState({})
  const [showFull, setShowFull] = useState(false)
  const [itemsTotal, setItemsTotal] = useState([])
  const [shippedTotal, setShippedTotal] = useState(0)
  const [sampleTotal, setSampleTotal] = useState(0)
  const [visualResult, setVisualResult] = useState({})

  const [constructTotal, setConstructTotal] = useState(false)
  const [inspectTotal, setInspectTotal] = useState(0)
  const [aqlSampleTotal, setAqlSampleTotal] = useState(0)
  const [updateHistory, setUpdateHistory] = useState([])
  const [showHistory, setShowHistory] = useState(false)
  const [sharePointFiles, setSharePointFiles] = useState([])
  const [showSharePointFiles, setShowSharePointFiles] = useState(false)
  const [checkListTemplate, setCheckListTemplate] = useState([])

  const [defectLanguage, setDefectLanguage] = useState("en")
  const [defectList, setDefectList] = useState([])
  const [criticalDefects, setCriticalDefects] = useState([])

  const [sharePointPath, setSharePointPath] = useState()
  const [mfrSearch, setMFRsearch] = useState("")

  const [packTypes, setPackTypes] = useState("")

  const [mqaMembers, setMQAmembers] = useState("")
  const [productCategories, setProductCategories] = useState("")
  const [inspType, setInspType] = useState("")

  // Data

  useEffect(() => {
    let root = document.querySelector(":root")
    const userAgent = navigator.userAgent.toLowerCase()
    const isTablet = /(ipad|tablet|(android(?!.*mobile))|(windows(?!.*phone)(.*touch))|kindle|playbook|silk|(puffin(?!.*(IP|AP|WP))))/.test(userAgent)

    // Remove padding of scroll width if it is tablet to make columns of ChangeTrack section aligned
    if (isTablet) {
      root.style.setProperty("--changeTrack_scroll_width", "7fr")
    } else {
      root.style.setProperty("--changeTrack_scroll_width", "6.7fr")
    }

    // check Safari browser
    if (userAgent.includes("safari") && userAgent.includes("macintosh")) {
      root.style.setProperty("--changeTrack_scroll_width", "7fr")
    }

    trackPromise(
      axios({
        url: "/api/getUserProfile",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        }
        //data: JSON.stringify({})   seems like this line has to be removed to avoid error from Heroku as this line is passing empty parameter.
      })
        .then(response => response.data)
        .then(response => {
          setUserProfile(response["userProfile"])
          setMCTable(response["mcTable"])
          setPartyTable(response["partyTable"])
          setMfList(response["mfList"])
          setAqlTable(response["aqlTable"])
          setCheckListTemplate(response["checkListTemplate"])
          setSharePointPath(response["sharePointPath"])
          setMQAmembers(response["mqaMembers"])
          setPackTypes(response["packTypes"])
          setProductCategories(response["productCategories"])
          setInspType(response["inspType"])
        })
        .catch(error => {
          console.log(error)
        }),
      "getUserProfile"
    )

    fetch("/api/getDefectTable", {
      method: "GET"
      // ,
      // "headers": {
      //   "x-rapidapi-host": "fairestdb.p.rapidapi.com",
      //   "x-rapidapi-key": "apikey"
      // }
    })
      .then(response => response.json())
      .then(response => {
        setDefectList(response)
      })
      .catch(err => {
        console.log(err)
      })

    fetch("/api/getCriticalDefects", {
      method: "GET"
      // ,
      // "headers": {
      //   "x-rapidapi-host": "fairestdb.p.rapidapi.com",
      //   "x-rapidapi-key": "apikey"
      // }
    })
      .then(response => response.json())
      .then(response => {
        setCriticalDefects(response)
      })
      .catch(err => {
        console.log(err)
      })
  }, [])

  useEffect(() => {
    var x = document.getElementsByClassName("itemTotal_Detail")
    var len = x.length
    var first_found = -1
    var i, j, long, scroll_y

    for (j = 0; j < len; j++) {
      x[j].style.backgroundColor = "#0746f54d" /*back to original color*/
    }

    for (i = 0; i < len; i++) {
      const currentChild = x[i].firstElementChild.innerHTML
      if (currentChild.includes(itemSearchString) && itemSearchString !== "") {
        x[i].style.backgroundColor = "yellow"
        if (first_found === -1) {
          first_found = i
        }
      }
    }

    if (first_found > 0) {
      document.getElementById("scroller").scrollTo(0, 0)
      long = document.getElementById("scroller").scrollHeight
      scroll_y = (long / len) * first_found
      document.getElementById("scroller").scrollBy(0, scroll_y)
    } else {
      document.getElementsByName("itemTotal_searchString")[0].value = ""
      //document.getElementsByName("itemTotal_searchString")[0].placeholder = "Not found! Try again!"
      document.getElementsByName("itemTotal_searchString")[0].placeholder = ""
    }

    //document.getElementsByName('mc_no')[0].value =  search_string    // trigger highlight in item table - itemTable block.

    x = document.getElementsByClassName("itemTable_Detail")
    len = x.length
    first_found = -1

    for (j = 0; j < len; j++) {
      x[j].style.backgroundColor = "#0746f54d" /*back to original color*/
    }

    for (i = 0; i < len; i++) {
      const currentChild = x[i].children[1].innerHTML
      if (currentChild.includes(itemSearchString) && itemSearchString !== "") {
        x[i].style.backgroundColor = "yellow"
        if (first_found === -1) {
          first_found = i
        }
      }
    }

    if (first_found > 0) {
      document.getElementById("itemTableScroller").scrollTo(0, 0)
      long = document.getElementById("itemTableScroller").scrollHeight
      scroll_y = (long / len) * first_found
      document.getElementById("itemTableScroller").scrollBy(0, scroll_y)
    }
  }, [itemSearchString, items])

  // calcuate itemsTotal based on items

  useEffect(() => {
    if (constructTotal) {
      const items1 = items
        ? items.map(function (obj) {
            return {
              item_no: obj.item_no,
              //order_qty: obj.order_qty,
              ship_qty: obj.ship_qty
            }
          })
        : null

      // use reduce to calculate total by item#
      const items2 = items
        ? items1.reduce((prev, next) => {
            if (next.item_no in prev) {
              prev[next.item_no].ship_qty = parseInt(prev[next.item_no].ship_qty) + parseInt(next.ship_qty)
            } else {
              prev[next.item_no] = next
            }
            return prev
          }, {})
        : null

      // present each item total
      const items3 = Object.keys(items2).map(item_no => items2[item_no])

      var _shippedTotal = 0
      for (var i = 0; i < items3.length; i += 1) {
        _shippedTotal = _shippedTotal + items3[i].ship_qty
      }

      let newItemsTotal = [...items3]
      for (i = 0; i < newItemsTotal.length; i += 1) {
        const ship_qty = newItemsTotal[i].ship_qty
        const sampleSize = getSampleSize(aqlTable, ship_qty, _shippedTotal)
        let inspectQty = sampleSize
        newItemsTotal[i] = { ...newItemsTotal[i], sample_qty: sampleSize, inspect_qty: inspectQty }
      }

      setItemsTotal(newItemsTotal)
      setConstructTotal(false)
    }
  }, [items, constructTotal, aqlTable, shippedTotal])

  // update label based on items change.
  useEffect(() => {
    // update label
    const labels = items.map(({ label }) => label)
    const uniqueLabels = [...new Set(labels)]
    setMisc(prev => ({ ...prev, label: uniqueLabels.sort().join() }))
  }, [items])

  function getSampleSize(_aql, ship_qty, shippedTotal) {
    if (shippedTotal === 0 || isNaN(ship_qty)) {
      return 0
    }
    const aql = _aql.filter(x => x.size_start <= shippedTotal && x.size_end >= shippedTotal)
    const sampleQty = (ship_qty / shippedTotal) * aql[0]["25sample"]
    return Math.round(sampleQty) //  > 0.5 roundup to integer
  }

  // ****

  // updateMiscResult

  useEffect(() => {
    var sampleThreshold = ""
    var defectiveResult = ""
    var visualResult = ""

    var sampleThresholdMajor = 0
    var sampleThresholdMinor = 0

    var total_major = 0
    var total_minor = 0
    var total_critical = 0

    for (var i = 0; i < defects.length; i += 1) {
      if (defects[i].critical) {
        total_critical = total_critical + parseInt(defects[i].major)
      } else if (parseInt(defects[i].major) > 0) {
        total_major = total_major + parseInt(defects[i].major)
      }

      if (parseInt(defects[i].minor) > 0) {
        total_minor = total_minor + parseInt(defects[i].minor)
      }
    }

    if (shippedTotal) {
      const aql = aqlTable.filter(x => x.size_start <= shippedTotal && x.size_end >= shippedTotal)
      sampleThreshold = aql[0]["25accept"].toString() + " / " + aql[0]["40accept"].toString()
      sampleThresholdMajor = aql[0]["25accept"]
      sampleThresholdMinor = aql[0]["40accept"]
    }

    // Check Inspection Result -  1) If Critical -> Failed,  2) Major + (Minor/4) > Threshold of Major -> Failed, 3) Packing Failed or Measurement Failed -> Failed

    if (total_critical + total_major + total_minor > 0) {
      defectiveResult = " " + total_critical.toString() + " / " + total_major.toString() + " / " + total_minor.toString()

      if (total_major + Math.floor(total_minor / 4) > sampleThresholdMajor) {
        visualResult = "Failed"
      } else if (total_minor > sampleThresholdMinor) {
        visualResult = "Failed"
      }
    }

    if (total_critical > 0) {
      visualResult = "Failed"
    }

    if (visualResult !== "Failed" && defectiveResult.length > 0) {
      visualResult = "Passed"
    }

    setVisualResult({ visual_result: visualResult, defective_result: defectiveResult, sample_threshold: sampleThreshold })
  }, [defects, aqlTable, shippedTotal])

  const handleSupplierInfo = (su, mf) => {
    partyTable.map(x => (x.SU === su ? setCurrentSUName(x.SU_NAME) : null))
    partyTable.map(x => (x.MF === mf ? setCurrentMFName(x.MF_NAME) : null))
  }

  // CRUD operations
  const addMC = mc => {
    mcTable.map(
      x => (x.mc_no === mc ? ((x.id = uuidv4()), setItems(prev => prev.concat(x)), setMisc({ ...misc, ship_window: x.ship_window, product_type: x.item_desc, fibre_content: x.fabric_note })) : null)
      // setDefaultShipWindow(x.ship_window), setDefaultFibreContent(x.fabric_note), setDefaultProductType(x.item_desc
    )
    handleSupplierInfo(currentSU, currentMF)
    setConstructTotal(true)
    setShowFull(true)
    setEditMode(true)
    fillCheckList() // since it's empty
  }

  const deleteAllItems = item_no => {
    // setEditing(false)
    setItems(items.filter(item => item.item_no !== item_no))
    setItemsTotal(itemsTotal.filter(item => item.item_no !== item_no))
  }

  const deleteItem = id => {
    // setEditing(false)
    //setItems(items.filter(item => item.id !== id))
  }

  const addToDefectList = (product, defect_area, defect_detail, major, minor, defect_area_code, defect_detail_area, defect_language) => {
    setDefects(prev => prev.concat({ id: uuidv4(), product, defect_area, defect_detail, major, minor, defect_area_code, defect_detail_area, defect_language, critical: isCriticalDefect(product, defect_detail_area) }))
  }

  const deleteDefect = id => {
    // setEditing(false)
    setDefects(defects.filter(defect => defect.id !== id))
  }

  const setDisplayAlert = alert => {
    //{ message: "hi", position: 0, timeout: 3000, color: "alert__green" }
    setAlert(alert)
    setTimeout(function () {
      setAlert({ position: 100 })
    }, alert.timeout)
  }

  const handleCheckItemChange = (id, change) => {
    const newCheckList = [...checkList]
    const index = newCheckList.findIndex(r => r.id === id)
    newCheckList[index] = change
    setCheckList(newCheckList)
    //setCheckList([...checkList], { id: change })
  }

  const undoTable = () => {
    setItems(JSON.parse(localStorage.getItem("items")))
    setItemsTotal(JSON.parse(localStorage.getItem("itemsTotal")))
  }

  const resetInspectQty = () => {
    const _total = [...itemsTotal]
    for (var i = 0; i < _total.length; i++) {
      const _sample_qty = getSampleSize(aqlTable, _total[i].ship_qty, shippedTotal)
      _total[i].sample_qty = _sample_qty
      _total[i].inspect_qty = _sample_qty
    }
    setItemsTotal(_total)
  }

  const handleSave = submitted => {
    // Convert inspection date to time 00.00.00
    const main = { su_no: currentSU, mf_no: currentMF, inspection_date: inspectionDate.toDateString(), submitted: submitted }
    const _id = { mc: inspectionID.mc, iteration: inspectionID.iteration, type: inspectionID.type }
    let newMisc = { ...misc }
    newMisc = { ...newMisc, final_result: calculateFinalResult(), visual_result: visualResult, defectLanguage: defectLanguage, submitted: submitted }

    trackPromise(
      axios({
        url: "/api/save",
        method: "POST",
        headers: {
          "Content-type": "application/json"
        },
        data: JSON.stringify({ _id: _id, main: main, misc: newMisc, mf: currentMF, checkList: checkList, items: items, itemsTotal: itemsTotal, defects: defects, uploads: uploads, updateHistory: updateHistory })
      })
        .then(res => {
          clearScreen()
          setDisplayAlert({ message: "Saved Successfully !", position: 0, timeout: 1000, color: "alert__green" })
        })
        .catch(err => {
          console.log(err.response.data)
          setDisplayAlert({ message: "There was an error in saving, please contact the admin !!", position: 0, timeout: 2000, color: "alert__red" })
        }),
      "saveInspection"
    )
  }

  const handleSearch = id => {
    //console.log("entering api/search")

    trackPromise(
      axios({
        url: "/api/search",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        },
        data: JSON.stringify({ _id: id })
      })
        // .then(response => {
        //   console.log("status", response.status)
        // })
        .then(response => response.data)
        .then(response => {
          setShowFull(true)
          setItems(response["items"])
          setItemsTotal(response["itemsTotal"])

          localStorage.setItem("items", JSON.stringify(response["items"]))
          localStorage.setItem("itemsTotal", JSON.stringify(response["itemsTotal"]))

          setInspectionDate(response["main"].inspection_date ? new Date(response["main"].inspection_date) : null)
          setMain(response["main"])
          setMisc(response["misc"] ? response["misc"] : {})
          if (response["misc"]["defectLanguage"] === undefined) {
            setDefectLanguage("en")
          } else {
            setDefectLanguage(response["misc"]["defectLanguage"])
          }

          setCheckList(response["checkList"])

          handleSupplierInfo(response["main"].su_no, response["main"].mf_no)

          if (response["defects"] === undefined) {
            setDefects([])
          } else {
            //console.log(response["defects"])
            setDefects(response["defects"])
          }

          if (response["uploads"] === undefined) {
            setUploads("")
          } else {
            //console.log(response["uploads"])
            setUploads(response["uploads"])
          }
          if (response["update_history"] === undefined) {
            setUpdateHistory([])
          } else {
            setUpdateHistory(response["update_history"])
          }
          setSharePointFiles(false)
          setShowSharePointFiles(false)
          setShowHistory(false)
        })
        .catch(err => {
          //console.log("err", err.response.status)
          if (err.response.status === 406) {
            setDisplayAlert({ message: "Not a valid MC for this manufacturer !", position: 0, timeout: 2000, color: "alert__red" })
          } else if (err.response.status === 501) {
            window.location = "/login/timeout"
          } else setDisplayAlert({ message: "No such Inspection ID, please try again !", position: 0, timeout: 2000, color: "alert__red" })
        }),
      "searchKey"
    )
  }

  const handleDelete = id => {
    var confirm = window.confirm("Are you sure you want to delete this inspection ? ")
    if (confirm !== true) {
      return
    }
    trackPromise(
      axios({
        url: "/api/delete",
        method: "POST",
        headers: {
          "Content-type": "application/json"
        },
        data: JSON.stringify({ _id: id })
      })
        .then(response => (response.status === 200 ? (clearScreen(), setDisplayAlert({ message: "Deleted successfully !", position: 0, timeout: 1500, color: "alert__green" })) : null))
        .catch(err => {
          setDisplayAlert({ message: "Error deleting the inspection ID !", position: 0, timeout: 1500, color: "alert__red" })
        }),
      "deleteInspection"
    )
  }

  const clearScreen = () => {
    setItems([])
    setDefects([])
    setCheckList([])
    setSearchedMC([])
    setUploads([])
    setShowSearchedMC(false)
    setMisc([])
    setUpdateHistory([])
    setShowFull(false)
    setEditMode(false)
    setShowHistory(false)
    setSharePointFiles([])
    setShowSharePointFiles(false)
    setShowHistory(true)
  }

  // ** implement delay function
  const delay = ms => new Promise(res => setTimeout(res, ms))

  const handleSessionTimeOut = async () => {
    setDisplayAlert({ message: "Session timeout, please login again !", position: 0, timeout: 3000, color: "alert__red" })
    await delay(3000)
    window.location = "/login/timeout"
  }

  const searchInspByMC = mc => {
    setShowSearchedMC(true)

    trackPromise(
      //fetch("/api/searchInspByMC", {
      axios({
        url: "/api/searchInspByMC",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        },
        data: JSON.stringify({ mc: mc, mfList: mfList })
      })
        .then(response => response.data)
        .then(response => {
          setSearchedMC(response)
          setSharePointFiles(false)
        })
        .catch(err => {
          //console.log('err', err)
          err.response.status === 501 ? handleSessionTimeOut() : setDisplayAlert({ message: "Something went wrong !", position: 0, timeout: 2000, color: "alert__red" })
        }),
      "mfrSelection"
    )
  }

  const downloadDoc = (_id, file_name) => event => {
    //event.preventDefault()

    fetch("/download/" + _id, {
      method: "GET",
      headers: {
        "Content-Type": "application/octet-stream",
        "Content-Disposition": "attachment"
      }
    })
      .then(response => response.blob())
      .then(blob => {
        // Create blob link to download
        const url = window.URL.createObjectURL(new Blob([blob]))
        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", file_name)

        // Append to html link element page
        document.body.appendChild(link)

        // Start download
        link.click()

        // Clean up and remove the link
        link.parentNode.removeChild(link)
      })
  }

  const downloadSharePointFile = file_name => {
    //event.preventDefault()

    const file_url = "/sites/OSO/".concat(sharePointPath, "/", "SU", currentSU, "MF", currentMF, "/", file_name)

    trackPromise(
      fetch("/downloadSharePointFile", {
        method: "GET",
        headers: {
          "Content-Type": "application/octet-stream",
          "Content-Disposition": "attachment",
          "Cache-Control": "no-cache",
          file_url: file_url
        }
      })
        .then(response => response.blob())
        .then(blob => {
          // Create blob link to download
          const url = window.URL.createObjectURL(new Blob([blob]))
          const link = document.createElement("a")
          link.href = url
          link.setAttribute("download", file_name)

          // Append to html link element page
          document.body.appendChild(link)

          // Start download
          link.click()

          // Clean up and remove the link
          link.parentNode.removeChild(link)
        }),
      "downloadSharePointfiles"
    )
  }

  const printReport = (file_name, draft) => {
    //event.preventDefault()
    if (misc.qa_type === undefined) {
      setDisplayAlert({ message: "QA Type must be chosen !", position: 0, timeout: 2000, color: "alert__red" })
      return
    }
    if (misc.qa_type === "MQA" && (misc.mqa === undefined || misc.mqa === "Select")) {
      setDisplayAlert({ message: "MQA must be specified !", position: 0, timeout: 2000, color: "alert__red" })
      return
    }

    file_name = "MMG Inspection Report - " + inspectionID["mc"] + "-" + inspectionID["iteration"] + "-" + inspectionID["type"] + (draft ? " (Draft) " : "") + ".xlsx"

    trackPromise(
      fetch("/printreport", {
        method: "POST",
        headers: {
          "Content-Type": "application/octet-stream",
          "Content-Disposition": "attachment",
          "Cache-Control": "no-cache",
          inspectionID: JSON.stringify({ inspectionID }),
          localTime: JSON.stringify(convertToLocal(Date.now()))
        }
      })
        .then(response => response.blob())
        .then(blob => {
          // Create blob link to download
          const url = window.URL.createObjectURL(new Blob([blob]))
          const link = document.createElement("a")
          link.href = url
          link.setAttribute("download", file_name)

          // Append to html link element page
          document.body.appendChild(link)

          // Start download
          link.click()

          // Clean up and remove the link
          link.parentNode.removeChild(link)
        }),
      "printReport"
    )
  }

  const getSharePointFiles = () => {
    trackPromise(
      axios({
        url: "/api/getsharepointfiles",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        },
        data: JSON.stringify({ folder: "SU" + currentSU + "MF" + currentMF, inspection_id: inspectionID.mc + "-" + inspectionID.iteration + "-" + inspectionID.type })
      })
        .then(response => response.data)
        .then(response => {
          setSharePointFiles(response)
        })
        .catch(err => {
          if (err.response.status === 404) {
            setDisplayAlert({ message: "Folder on SharePoint was not created !", position: 3, timeout: 2000, color: "alert__red" })
          }
        }),
      "sharePointFiles"
    )
  }

  const createsharePointPDF = () => {
    var confirm = window.confirm("You're recommended to use Microsoft Lens to create PDF file.  For non-chart photos with lesser resolution (around 400px *400px), you can try this function but all photo files will be zipped !  Please use with care !")
    if (confirm !== true) {
      return
    }
    const inspection_date = inspectionDate.getMonth() + 1 + "/" + inspectionDate.getDate() + "/" + inspectionDate.getFullYear()
    trackPromise(
      axios({
        url: "/api/createsharepointpdf",
        timeout: 1000 * 28, // Wait for 28 seconds
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes",
          inspection_id: inspectionID.mc + "-" + inspectionID.iteration + "-" + inspectionID.type,
          //relative_url: "2022InspRpt/SU" + currentSU + "MF" + currentMF
          relative_url: sharePointPath.concat("/", "SU", currentSU, "MF", currentMF),
          inspection_date: inspection_date
        },
        data: JSON.stringify({ folder: "SU" + currentSU + "MF" + currentMF, inspection_id: inspectionID.mc + "-" + inspectionID.iteration + "-" + inspectionID.type })
      })
        .then(response => response.data)
        .then(response => {
          setSharePointFiles(response)
          document.getElementById("sharePoint_refresh").click()
        })
        .catch(err => {
          const errMsg = err.message
          if (errMsg.includes("timeout")) {
            setDisplayAlert({ message: "Warning : Server timeout interruption and some files may be lost ! Please refresh SharePoint status to make sure all photos are there and reduce the size of your photos and resubmit as neccessary, or use Microsft Lens on your mobile device!", position: 3, timeout: 9000, color: "alert__red" })
          }
          if (err.response.status === 404) {
            setDisplayAlert({ message: "PDF creation error !", position: 3, timeout: 2000, color: "alert__red" })
          } else if (err.response.status === 405) {
            setDisplayAlert({ message: "No photos to generate PDF !", position: 3, timeout: 2000, color: "alert__red" })
          }
        }),
      "sharePointFiles"
    )
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 0, timeout: 3000, color: "alert__red" })
  }

  const checkDuplicateID = id => {
    trackPromise(
      axios({
        url: "/api/checkDuplicateID",
        method: "POST",
        headers: {
          "Content-type": "application/json"
        },
        data: JSON.stringify({ _id: id })
      }).then(res => (res.status === 200 ? displayAlert("This key has been used by previous inspection ! Please check and or consider a new iteration!") : addMC(id.mc))),
      "createInspection"
    )
  }

  const handleDeleteSPfile = id => {
    var confirm = window.confirm("Are you sure you want to delete the SharePoint file ? ")
    if (confirm !== true) {
      return
    }
    trackPromise(
      axios({
        url: "/api/deleteSPfile",
        method: "POST",
        headers: {
          "Content-type": "application/json"
        },
        data: JSON.stringify({ _id: id })
      })
        .then(response => (response.status === 200 ? (setDisplayAlert({ message: "SharePoint file deleted successfully !", position: 3, timeout: 3000, color: "alert__green" }), document.getElementById("sharePoint_refresh").click()) : null))
        .catch(err => {
          setDisplayAlert({ message: "Error deleting the SP file !", position: 4, timeout: 1500, color: "alert__red" })
        }),
      "deleteInspection"
    )
  }

  function isCriticalDefect(product, defect) {
    for (var i = 0; i < criticalDefects.length; i += 1) {
      if (criticalDefects[i].product === product && criticalDefects[i].defect_detail_code === defect) {
        return true
      }
    }
    return false
  }

  function calculateFinalResult() {
    let final_result = ""
    if (misc.measurement_result === "Failed" || misc.packing_result === "Failed" || visualResult.visual_result === "Failed") {
      final_result = "Failed"
    } else if (visualResult.visual_result === "") {
      final_result = "Passed"
    } else {
      final_result = "Passed"
    }
    return final_result
  }

  // convert to local time
  function convertToLocal(date) {
    const t = new Date(date).toTimeString()
    const d = new Date(date).toDateString()
    var local = d + " " + t
    var local_end = local.search("GMT+") - 1
    local = local.substring(0, local_end)
    return local
  }

  const fillCheckList = () => {
    // Populate values to the checklist template.
    const newCheckList = [...checkListTemplate]
    newCheckList.sort((a, b) => a.id - b.id) // must sort to present in order...
    setCheckList(newCheckList)
  }

  return (
    <Fragment>
      {/* <UserProfile userProfile={userProfile} /> */}
      <SupplierInfo currentSU={currentSU} currentMF={currentMF} currentSUName={currentSUName} currentMFName={currentMFName} setSelectingSupplier={setSelectingSupplier} setShowFull={setShowFull} setMFRsearch={setMFRsearch} />
      <MfrSelection partyTable={partyTable} setCurrentSU={setCurrentSU} setCurrentMF={setCurrentMF} setCurrentSUName={setCurrentSUName} setCurrentMFName={setCurrentMFName} selectingSupplier={selectingSupplier} setSelectingSupplier={setSelectingSupplier} setMFRsearch={setMFRsearch} mfrSearch={mfrSearch} />

      <ShowSpinner position={1} />
      <MainMenu handleSave={handleSave} handleSearch={handleSearch} inspectionID={inspectionID} handleDelete={handleDelete} printReport={printReport} editMode={editMode} setEditMode={setEditMode} userProfile={userProfile} main={main} misc={misc} showFull={showFull} clearScreen={clearScreen} updateHistory={updateHistory} />

      <div className="flex-row">
        <div className="flex-large">
          <Fragment>
            {/* <h2>Search Inspection</h2> */}
            <SearchKey checkDuplicateID={checkDuplicateID} mcTable={mcTable} setDisplayAlert={setDisplayAlert} inspectionID={inspectionID} setInspectionID={setInspectionID} clearScreen={clearScreen} searchInspByMC={searchInspByMC} currentSU={currentSU} currentMF={currentMF} editMode={editMode} inspType={inspType} />

            {showSearchedMC ? <SearchedMC inspectionID={inspectionID} setInspectionID={setInspectionID} handleSearch={handleSearch} searchInspByMC={searchInspByMC} searchedMC={searchedMC} setShowSearchedMC={setShowSearchedMC} mcTable={mcTable} currentSU={currentSU} currentMF={currentMF} /> : null}
          </Fragment>
        </div>
      </div>
      {alert.position === 0 ? (
        <div className="flex-row">
          <div className="flex-large">
            <ShowAlert alert={alert} />
          </div>
        </div>
      ) : null}
      <ShowSpinner position={2} />

      {/* Result  */}
      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large">
          <Fragment>
            <Result setInspectionDate={setInspectionDate} inspectionDate={inspectionDate} setMisc={setMisc} misc={misc} editMode={editMode} isCriticalDefect={isCriticalDefect} aqlTable={aqlTable} shippedTotal={shippedTotal} visualResult={visualResult} calculateFinalResult={calculateFinalResult}></Result>
          </Fragment>
        </div>
      </div>

      {/* Misc section with inspection Date  */}
      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large">
          <Fragment>
            <Misc setInspectionDate={setInspectionDate} inspectionDate={inspectionDate} setMisc={setMisc} misc={misc} editMode={editMode} mqaMembers={mqaMembers} packTypes={packTypes} productCategories={productCategories} userProfile={userProfile} items={items}></Misc>
          </Fragment>
        </div>
      </div>

      {alert.position === 1 ? (
        <div className="flex-row">
          <div className="flex-large">
            <ShowAlert alert={alert} />
          </div>
        </div>
      ) : null}
      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large">
          <div className="itemTable_2col">
            <ItemTable items={items} deleteItem={deleteItem} setItems={setItems} setDisplayAlert={setDisplayAlert} mcTable={mcTable} addMC={addMC} editMode={editMode} currentSU={currentSU} currentMF={currentMF} />
            <div></div>
            <ItemTotal items={items} deleteAllItems={deleteAllItems} setItemSearchString={setItemSearchString} aqlTable={aqlTable} editMode={editMode} showFull={showFull} shippedTotal={shippedTotal} sampleTotal={sampleTotal} itemsTotal={itemsTotal} getSampleSize={getSampleSize} setItemsTotal={setItemsTotal} setShippedTotal={setShippedTotal} setSampleTotal={setSampleTotal} inspectTotal={inspectTotal} setInspectTotal={setInspectTotal} setAqlSampleTotal={setAqlSampleTotal} aqlSampleTotal={aqlSampleTotal} undoTable={undoTable} resetInspectQty={resetInspectQty} setDisplayAlert={setDisplayAlert} />
          </div>
        </div>
      </div>
      <div className="flex-row">
        <div className={"flex-large " + (showFull ? null : "hideSection")}>
          <DefectOptions items={items} addToDefectList={addToDefectList} setDisplayAlert={setDisplayAlert} editMode={editMode} defects={defects} defectLanguage={defectLanguage} setDefectLanguage={setDefectLanguage} defectList={defectList} showFull={showFull} />
        </div>
      </div>
      {alert.position === 2 ? (
        <div className="flex-row">
          <div className="flex-large">
            <ShowAlert alert={alert} />
          </div>
        </div>
      ) : null}
      <div className="flex-row">
        <div className={"flex-large " + (showFull ? null : "hideSection")}>
          <DefectList defects={defects} deleteDefect={deleteDefect} editMode={editMode} isCriticalDefect={isCriticalDefect} defectLanguage={defectLanguage} defectList={defectList} />
        </div>
      </div>
      <div className="flex-row">
        <div className={"flex-large " + (showFull ? null : "hideSection")}>
          <CheckList checkList={checkList} handleCheckItemChange={handleCheckItemChange} editMode={editMode} />
        </div>
      </div>
      {alert.position === 3 ? (
        <div className="flex-row">
          <div className="flex-large">
            <ShowAlert alert={alert} />
          </div>
        </div>
      ) : null}

      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large ">
          <SharePoint currentSU={currentSU} currentMF={currentMF} inspectionID={inspectionID} editMode={editMode} getSharePointFiles={getSharePointFiles} setShowSharePointFiles={setShowSharePointFiles} showSharePointFiles={showSharePointFiles} setSharePointFiles={setSharePointFiles} sharePointPath={sharePointPath} createsharePointPDF={createsharePointPDF} />
          <ShowSpinner position={3} />
        </div>
      </div>

      {alert.position === 4 ? (
        <div className="flex-row">
          <div className="flex-large">
            <ShowAlert alert={alert} />
          </div>
        </div>
      ) : null}

      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large ">
          <ShowSharePointFiles sharePointFiles={sharePointFiles} downloadSharePointFile={downloadSharePointFile} showSharePointFiles={showSharePointFiles} convertToLocal={convertToLocal} editMode={editMode} handleDeleteSPfile={handleDeleteSPfile} />
        </div>
      </div>

      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        {/* <div className={"flex-row  hideSection"}> */}
        <div className="flex-large ">
          <UploadForm setDisplayAlert={setDisplayAlert} inspectionID={inspectionID} setUploads={setUploads} uploads={uploads} downloadDoc={downloadDoc} editMode={editMode} currentSU={currentSU} currentMF={currentMF} setShowSharePointFiles={setShowSharePointFiles} sharePointPath={sharePointPath} inspectionDate={inspectionDate} />
        </div>

        {/* <div className="flex-large">
          <PhotoBlock uploadTable={uploads} downloadDoc={downloadDoc} currentSU={currentSU} currentMF={currentMF} inspectionID={inspectionID} />
        </div> */}
      </div>

      <div className={"flex-row " + (showFull ? null : "hideSection")}>
        <div className="flex-large ">
          <ChangeTrack updateHistory={updateHistory} showHistory={showHistory} setShowHistory={setShowHistory} convertToLocal={convertToLocal} />
        </div>
      </div>
      <div>&nbsp;</div>
      <div>&nbsp;</div>
    </Fragment>
  )
}
export default App
